
.app-header {
  padding: 5px;
  display: flex;
  flex-direction: column;
  z-index: var(--zIndex);
  transition: all 0.3s ease-in-out;

  .top-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .lists-tab {
    margin: 0 -5px;

    &.slider {
      display: none;
      z-index: var(--zIndex);
    }

    .list-tab-button {
      // margin: 0 10px 0 0;
    }

    @media (max-width: 700px) {
      &.slider {
        display: block;
      }
    }

    .swiper-slide {
      padding: 8px 0;
      width: auto;
    }
  }

  @media (max-width: 768px) {
    margin: 0 auto 0;
  }

  .buttons-box {
    display: flex;
  }

  .settings-btn {
    background-color: whitesmoke;
    padding: 4px;
    border: none;
    border-radius: 100px;
    width: 40px;
    height: 40px;
    margin-right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;

    svg {
      width: 70%;
    }
  }

  .about-btn {
    background-color: whitesmoke;
    padding: 7px 12px 8px;
    border: none;
    border-radius: 20px;
    min-width: 120px;
    height: 40px;
  }
}

.logo-box {
  width: 128px;
  margin-top: 4px;

  img {
    width: 100%;
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(239, 239, 239);
  padding: 40px 15px;
  z-index: calc(var(--zIndex) + 1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-y: scroll;
  overflow-x: hidden;

  &.channels-modal {
    justify-content: flex-start;
    align-items: flex-start;
    padding: 40px 15px 0;
  }

  @media (max-width: 768px) {
    justify-content: flex-start;
    align-items: flex-start;
  }

  p {
    max-width: 450px;
    white-space: pre-wrap;
    text-align: justify;
    text-align-last: left;
    line-height: 26px;

    a {
      color: #0057b7;
      font-weight: bold;
      text-decoration: underline;
    }
  }
}
