
.default-layout {
  height: 100%;
}

.container {
  position: relative;
  max-width: 450px;
  width: 100%;
  margin: 0 auto;

  @media (min-width: 450px) and (max-width: 700px) {
    max-width: 650px;
    padding: 0 15px;
  }
}

.main-box {
  display: flex;
  flex-direction: column;
  min-height: calc(100% - 126px);
}

.channels-modal {
  .close-btn {
    position: fixed !important;
  }
}

.footer-desc {
  font-size: 14px;
  color: #000;
  text-align: center;
  width: 100%;
  padding: 5px 0;
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: transparent;
  border: none;
  font-size: 30px;
  font-weight: bold;
  transform: rotate(45deg);

  &:active {
    transform: rotate(45deg) scale(0.9);
  }
}

.cookiePanel {
  position: fixed;
  width: 100%;
  bottom: 0;
  z-index: 2;
  background-color: #0057b7;
  color: #fff;
  padding: 10px;

  p {
    padding-top: 8px;
    // padding-left: 12px;
    width: 90%;
    font-size: 14px;
    text-align: justify;
  }

  .close-btn {
    color: #fff;
    right: 10px;
  }
}
